import { useSearchParams } from 'react-router-dom';
import { motion } from "framer-motion";

import { Welcome } from '.././pages/Welcome';
import { Login } from '.././pages/Login';

import Box from '@mui/joy/Box';

import { useColorScheme } from '@mui/joy/styles';
import { useEffect } from 'react';

function Error({ go }) {
    return (
        <div className='box'>
            <h1> <b>404</b></h1>
            <button onClick={() => { go(""); }}> </button>
        </div>
    );
}



export function Layout() {
    const { mode, setMode } = useColorScheme();
    const [searchParams, setSearchParams] = useSearchParams();
    const searchPage = searchParams.get('p') || '';
    var showPage = <Welcome />

    function Animation() {
        useEffect(() => {
            setMode('system');
        }, []);
        return (
            <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{
                    duration: 0.2,
                    delay: 0.2,
                }}>

                {showPage}
            </motion.div>
        );
    }

    function go(to = '') {
        setSearchParams({ p: to });
    }


    if (searchPage === '') {
        showPage = <Welcome searchParams={searchParams} setSearchParams={setSearchParams} go={go} />
    } else if (searchPage === 'login') {
        showPage = <Login searchParams={searchParams} setSearchParams={setSearchParams} go={go} />
    } else {
        showPage = <Error go={go} />
    }

    return (
        <Box sx={{
            padding: '0px',
            margin: '0px',
        }}>
            <main>
                {/* {showPage} */}
                <Animation />
            </main>
        </Box>
    );

}