import { useState } from 'react';
import useMediaQuery from '@mui/material/useMediaQuery';

// import Sheet from '@mui/joy/Sheet';
import Typography from '@mui/joy/Typography';
import Box from '@mui/joy/Box';
import Button from '@mui/joy/Button';
import TextField from '@mui/joy/TextField';
import Modal from '@mui/joy/Modal';
import ModalDialog from '@mui/joy/ModalDialog';
import Stack from '@mui/joy/Stack';
import Card from '@mui/joy/Card';
import LinearProgress from '@mui/joy/LinearProgress';

import ChatIcon from '@mui/icons-material/Chat';

import { motion } from "framer-motion";

export function Welcome({ searchParams, setSearchParams, go }) {
    const [open, setOpen] = useState(false);
    const [sendSuccess, setSendSucces] = useState(false);
    const matches = useMediaQuery('(min-width:800px)');
    return (
        <Box sx={{
            display: "flex",
            flexDirection: matches ? 'row' : 'column',
            justifyContent: matches ? 'space-around' : 'center',
            alignItems: 'center',
            background: "url('https://images.unsplash.com/photo-1606055854326-12a2fdcac6c0?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2070&q=80') no-repeat",
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundBlendMode: 'multiply',
            backgroundColor: '#9c9c9c',
            minHeight: '100vh',
            overflow: 'hidden',
            fontFamily: 'Inter'
        }}>
            <Stack sx={{
                backgroundColor: 'transparent',
                // margin: 'px'
                padding: '2% 5% '
            }}>
                <Typography level="h1" sx={{
                    color: '#FFFFFF',
                    fontWeight: '800',
                    // fontFamily: 'Noto Sans'
                }}>
                     !
                </Typography>
            </Stack>
            <Box sx={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                padding: matches ? '1.5rem' : '4% 7%'
            }}>
                <Card sx={{
                    // backgroundColor: 'rgba(200, 200, 200, 0.8)',
                    maxWidth: '310px',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flexDirection: 'column',
                    padding: matches ? '1.2rem' : '1rem'
                }}>
                    <Typography level="h4" fontWeight='700' lineHeight={1.3} sx={{
                        maxWidth: '98%',
                        // textAlign: 'center'
                        fontFamily: 'Inter'
                    }}>
                         ,   
                    </Typography>

                    <Button
                        color="primary"
                        size="lg"
                        variant="solid"
                        sx={{
                            margin: '15px',
                            width: '100%'
                        }}
                        onClick={() => setOpen(true)}
                    >
                        
                    </Button>
                </Card>
                <Card sx={{
                    maxWidth: '310px',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flexDirection: 'column',
                    margin: '7% 0%'
                }}>
                    <Typography level='h6' fontWeight={700} sx={{
                        fontFamily: 'Inter'
                    }}>     @DmitryStr</Typography>
                </Card>
            </Box>

            <Modal open={open} onClose={() => {
                setOpen(false);
                setSendSucces(false);
            }}>

                <ModalDialog
                    aria-labelledby="basic-modal-dialog-title"
                    aria-describedby="basic-modal-dialog-description"
                    sx={{
                        maxWidth: 550,
                        borderRadius: 'md',
                        p: 3,
                        boxShadow: 'lg',
                    }}>
                    {

                        !sendSuccess ?
                            <PageAnimation page={<InputPage />} />
                            :
                            <PageAnimation page={<SuccessPage />} />
                    }

                </ModalDialog>
            </Modal>
        </Box >

    );
    function InputPage() {
        return (
            <Box>
                <Typography
                    id="basic-modal-dialog-title"
                    component="h1"
                    level="inherit"
                    fontSize="1.75em"
                    mb="0.5em"
                    sx={{
                        // fontFamily: 'Montserrat'
                        fontFamily: 'Inter'
                    }}
                >
                    
                </Typography>

                <form
                    onSubmit={(event) => {
                        event.preventDefault();

                        setSendSucces(true);
                    }}
                >
                    <Stack spacing={4}>
                        <TextField label="  ID" size="lg" sx={{
                            fontWeight: '500', 
                            fontFamily: 'Inter',
                        }} autoFocus required />
                        <Button variant="soft" type="submit">
                            
                        </Button>
                    </Stack>
                </form>

            </Box>
        );
    }
    function SuccessPage() {
        return (
            <Stack spacing={2} sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                // fontFamily: 'Montserrat',
                fontFamily: 'Inter',
                maxWidth: '23rem'
            }}>
                <ChatIcon sx={{ fontSize: 56 }} />
                <Typography level="h4" fontWeight={700} textAlign="center" lineHeight={1.1} sx={{
                    fontFamily: 'Inter',
                    fontSize: '1.45rem'
                }}>
                        discord.    .
                </Typography>
                <Button type="button" variant="soft" sx={{
                    width: '50%'
                }} onClick={() => {
                    setOpen(false);
                    setSendSucces(false);
                }}>
                    
                </Button>
            </Stack>
        );
    }

    function PageAnimation({ page }) {
        return (
            <motion.div
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: 10 }}
                transition={{
                    duration: 0.2,
                    delay: 0.2,
                }} >
                {page}
            </motion.div>
        );
    }
}

