import Typography from '@mui/joy/Typography';
import Sheet from '@mui/joy/Sheet';
import Stack from '@mui/joy/Stack';
import LinearProgress from '@mui/joy/LinearProgress';
import CircularProgress from '@mui/joy/CircularProgress';


export function Login({ searchParams, setSearchParams, go }) {
    return (
        <Sheet sx={{
            display: "flex",
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            alignContent: 'center',
            minHeight: '100vh',
            overflow: 'hidden',
            fontFamily: 'Inter',
        }}>
            <Stack spacing={3} sx={{
                display: 'flex',
                alignItems: 'center'
            }}>
                <Typography level='h3' fontWeight={700}>
                      
                </Typography>

                <LinearProgress variant='soft' color='primary' size='lg' value={25} sx={{ width: '90%' }} />
                <CircularProgress size='md' value={30}/>
            </Stack>
        </Sheet>
    );
}